.page-contact{
  @include max-w(1140px){
    padding-bottom: 96px;
  }
  .banner{
    background-repeat: no-repeat;
    background-size: cover!important;
    //height: 469px;
    height: 475px;
    @include max-w(1430px){
      background-position: center!important;
    }
    @include max-w(666px){
      background-position: 59% top!important;
    }
    @include max-w(530px){
      height: 525px;
    }
    h2{
      max-width: 418px;
      color: #073949;
      text-align: left;
      line-height: 41px;
      padding-bottom: 13px;
      @include max-min-w(760px, 530px){
        max-width: 60%;
      }
      @include max-w(530px){
        max-width: 100%;
        line-height: 29px;
        padding-bottom: 210px;
      }
    }
    .container{
      height: 100%;
    }
  }
  .contact-form{
    max-width: 555px;
    margin: 0 auto;
    margin-top: 67px;
    border: 1px solid #ecf1f4;
    @include max-w(555px){
      padding: 0 10px;
    }
    p{
      margin: 0;
    }
    h2 + p{
      max-width: 320px;
      margin: 0 auto;
      margin-bottom: 30px;
      line-height: 23px;
      font-size: 18px;
      text-align: center;
    }
    h2{
      margin-top: 19px;
      margin-bottom: 41px;
    }
    input,textarea{
      max-width: 461px;
      margin: 0 auto;
      margin-top: 18px;
      display: block;
      font-style: italic;
      font-size: 18px;
      font-family: RobotoLight, sans-serif;
      color: $text-light-grey;
      padding: 0 10px;
    }
    input{
      height: 44px;
      &.send{
        height: 66px;
        max-width: 100%;
        font-family:  RobotoBold, sans-serif;
        font-size: 25px;
        font-style: normal;
        color: #fff;
        cursor: pointer;
        transition: all .3s;
        &:hover{
          background-color: rgba(0, 126, 180, 0.69);;
          transition: all .3s;
        }
      }
    }
    textarea{
      height: 166px;
      resize: vertical;
      padding-top: 7px;
    }
    textarea::placeholder,
    input::placeholder{
      color: #cbcccd;
      font-family: RobotoThin, sans-serif;
    }
    textarea:-ms-input-placeholder,
    input:-ms-input-placeholder{
      color: #cbcccd;
      font-family: RobotoThin, sans-serif;
    }
    textarea::-moz-placeholder,
    input::-moz-placeholder{
      color: #cbcccd;
    }
    input::-webkit-input-placeholder,
    textarea::-webkit-input-placeholder{
      color: #cbcccd;
      font-family: RobotoThin, sans-serif;
    }
    .capcha{
      max-width: 162px;
      height: 48px;
      margin: 19px auto 0 auto;
    }
    span.wpcf7-not-valid-tip {
      max-width: 461px;
      margin: 0 auto;
    }
    div.wpcf7 .ajax-loader,
    br{
      display: none;
    }
  }
}