//  Базовые стили

html {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
	background-color: $body-bg;
  color: $text-color;
	font-family: $font-family;
  font-weight: $font-weight;
	font-size: $font-size;
	line-height: $line-height;
}

a {
  color: $link-color;
  text-decoration: none;

  &:hover,
  &:active {
    color: $link-color--hover;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

section {
  position: relative;
}
ul{
  margin: 0;
  padding: 0;
}
li{
  list-style: none;
}
.page-container {
  max-width: $container-width;
  margin-left: auto;
  margin-right: auto;
  padding-left: $container-padding;
  padding-right: $container-padding;
}

.icon {
  display: inline-block;
}
.container{
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
  padding: 0 40px;
  @include max-w(530px){
    padding: 0 10px;
  }
}
.full-container{
  max-width: 1382px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}
.display-flex{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.flex-between{
  justify-content: space-between;
}
.flex-around{
  justify-content: space-around;
}
.flex-center{
  justify-content: center;
}
.flex-end{
  justify-content: flex-end;
}
.flex-align-center{
  align-items: center;
}
.underline{
  position: relative;
  &.active{
    &:after{
      border-color: $blue;
      transition: all .3s;
    }
  }
  &:after{
    content: "";
    display: inline-block;
    border-bottom: 1px solid #bababa;
    height: 1px;
    width: 100%;
    max-width: 165px;
    margin: 0 auto;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -16px;
    @include max-w(530px){
      bottom: 2px;
    }
    transition: all .3s;
  }
}
.v-line{
  &:before{
    content: "";
    background-color: #bababa;
    height: 158px;
    width: 1px;
    position: absolute;
    transition: all .3s;
    @include max-w(1471px){
      display: none;
    }
  }
  &:after{
    content: "";
    background-color: #bababa;
    height: 368px;
    width: 1px;
    position: absolute;
    transition: all .3s;
    @include max-w(1471px){
      display: none;
    }
  }
  &.active{
    &:before, &:after{
      background-color: $blue;
      transition: all .3s;
    }
  }
}
.mobile{
  display: none;
  @include max-w(530px){
    display: block;
  }
}
.desktop{
  display: block;
  @include max-w(530px){
    display: none;
  }
}