.page-footer {
	position: relative;
	padding: 96px 0 0 0;
	@include max-w(1140px){
		padding: 0;
	}
	.footer{
		background-color: #1c2629;
		padding: 27px 0 10px 0;
		@include max-w(530px){
			width: 100%;
			padding: 10px 0 10px 0;
		}
		&__top{
			position: relative;
			padding-bottom: 18px;
			&:after{
				content: "";
				position: absolute;
				bottom: 0;
				left: -15px;
				height: 1px;
				width: 105%;
				max-width: 1170px;
				background-color: $grey;
			}
			.logo{
				width: 26%;
				@include max-w(530px){
					width: 100%;
					max-width: 231px;
				}
				a{
					cursor: pointer;
				}
			}
			.block-share{
				width: 74%;
				@include max-w(1140px){
					padding-left: 54px;
				}
				@include max-w(530px){
					width: 100%;
					padding-left: 0;
				}
			}
		}
		&__bottom {
			padding: 16px 0;
			@include max-w(530px){
				padding: 20px 0 1px 0;
			}
			*{
				font-size: 16px;
				font-family: RobotoLight, sans-serif;
				line-height: 25px;
				color: #e4e9ec;
				transition: all .3s;
				@include max-w(530px){
					font-size: 14px;
					line-height: 16px;
				}
			}
			a{
				&:hover span{
					color: $blue;
					transition: all .3s;
				}
			}
			.privacy{
				width: 26%;
				@include max-w(530px){
					width: 100%;
				}
			}
			.copyright{
				width: 74%;
				@include max-w(1140px){
					padding-left: 54px;
				}
				@include max-w(530px){
					width: 100%;
					padding-left: 0;
				}
				p{
					margin: 3px 4px;
					@include max-w(530px){
						margin: 12px 0;
						line-height: 18px;
					}
				}
			}
		}
	}
	.social{
		padding: 1px 4px;
		@include max-w(530px){
			padding: 4px 0;
		}
		a{
			padding-right: 22px;
			font-size: 25px;
			color: $white;
			transition: all .3s;
			@include max-w(530px){
				padding-right: 16px;
				font-size: 20px;
			}
			&:hover{
				color: $blue;
				transition: all .3s;
			}
		}
	}
	.mail{
		font-size: 16px;
		padding: 8px 4px;
		@include max-w(530px){
			font-size: 14px;
			padding: 3px 4px;
		}
		span{
			font-family: RobotoLight, sans-serif;
			color: $white;
			padding-right: 8px;
		}
		a{
			font-family: RobotoBold, sans-serif;
			color: $white;
			transition: all .3s;
			&:hover{
				color: $blue;
				transition: all .3s;
			}
		}
	}
	.footer-info{
		background-color: #393d3e;
		p{
			font-size: 14px;
			color: #e4e9ec;
			line-height: 19px;
			margin: 0;
			padding: 22px 0 30px 0;
			@include max-w(1140px){
				padding: 15px 0 17px 0;
			}
			@include max-w(530px){
				padding: 14px 0 17px 0;
				font-size: 11px;
				line-height: 14px;
			}
		}
	}
}
