.page-header {
  position: relative;

}
.header-top{
  //min-height: 101px;
  background-color: #ffffff91;
  position: relative;
  z-index: 1;
  .container{
    height: 101px;
    align-items: center;
    @include max-w(1140px){
      height: 76px;
    }
    @include max-w(992px){
      height: 65px;
      //display: inline-block;
    }
    @include max-w(530px){
      height: 52px;
    }
  }
  .logo{
    padding-bottom: 32px;
    width: 24%;
    @include max-w(1140px){
      max-width: 164px;
      padding-bottom: 16px;
    }
    @include max-w(992px){
      min-width: 164px;
    }
  }
  .auth{
    width: 13%;
    text-align: right;
    @include max-w(992px){
      //width: 74%;
      width: calc(100% - 219px - 18px);
      padding: 0 0 8px 0;
    }
    @include max-w(530px){
      width: calc(100% - 166px - 18px);
    }
    a{
      color: $text-light-grey;
      font-size: 25px;
      transition: all .3s;
      @include max-w(530px){
        font-size: 18px;
      }
      &:hover{
        color: $blue;
        transition: all .3s;
      }
      &.user{
        padding-right: 28px;
        @include max-w(530px){
          padding-right: 10px;
        }
        &.active{
          color: $blue;
          &:hover{
            color: $text-light-grey;
          }
        }
      }
      &.login{
        padding-right: 11px;
      }
    }
  }
}

.menu{
  width: 66%;
  max-width: 632px;
  @include max-w(1140px){
    padding-left: 35px;
  }
  @include max-w(992px){
    width: 100%;
    padding-right: 40px;
    display: inline-block;
    max-width: 240px;
    position: absolute;
    right: 0;
    top: 65px;
    //background-color: #ffffff91;
    background-color: #ffffffe3;
    height: 0;
    padding-top: 0;
    padding-bottom: 0;
    overflow: hidden;
    transition: all .3s;
    text-align: left;
    @include max-w(530px){
      top: 52px;
    }
    a{
      line-height: 43px!important;
    }
    &.active{
      height: 265px;
      padding-bottom: 40px;
      padding-top: 10px;
      transition: all .3s;
      @include max-w(530px){
        height: 100vh;
      }
    }
  }

  ul{
    @include max-w(992px){
      display: inline-block;
    }
    li{
      a{
        font-family: RobotoLight, sans-serif;
        font-size: 16px;
        color: $text-light-grey;
        line-height: 25px;
        border-bottom: 1px solid transparent;
        transition: all .3s;
        @include max-w(1140px){
          font-size: 15px;
        }
        &:hover{
          border-bottom: 1px solid $blue;
          transition: all .3s;
        }
      }
    }
  }
}
.tablet-show{
  display: none;
  @include max-w(992px){
    display: inline-block;
    color: $text-light-grey;
    font-size: 23px;
    padding-bottom: 9px;
    transition: all .3s;
    @include max-w(530px){
      font-size: 15px;
    }
    &.active, &:hover{
      color: $blue;
      transition: all .3s;
    }
    &:active{
      color: $text-light-grey;
      transition: all .3s;
    }
  }
}