.banner{
  //background: url("../images/banner.jpg") top center no-repeat;
  margin-top: -101px;
  height: 959px;
  @include max-w(1140px){
    height: 870px;
    background-position: 59% top!important;
    background-size: 175%!important;
  }
  @include max-w(992px){
    height: 642px;
    background-size: cover!important;
  }
  @include max-w(530px){
    height: 522px;
    background-size: 530px!important;
    background-position: right -86px!important;
  }
  @include max-w(330px){
    height: 456px;
    background-size: 122%!important;
    background-position: 59% top!important;
  }
  &__text{
    max-width: 650px;
    @include max-w(1140px){
      line-height: 47px;
      padding: 0 0 47px 0;
    }
    @include max-w(880px){
      max-width: 68%;
    }
    @include max-w(530px){
      max-width: 100%;
    }
    h1{
      color: #073949;
      line-height: 57px;
      margin: 0;
      padding: 252px 0 41px 0;
      @include fluid-font(55px, 65px, 1024px, 1140px);
      @include max-w(1140px){
        line-height: 47px;
        padding: 225px 0 47px 0;
      }
      @include max-w(992px){
        padding-top: 195px;
        @include fluid-font(29px, 55px, 320px, 992px);
      }
      @include max-w(530px){
        padding-top: 124px;
        padding-bottom: 9px;
        line-height: 29px;
      }
    }
    p{
      padding-right: 65px;
      font-size: 18px;
      font-family: RobotoLight,sans-serif;
      line-height: 25px;
      @include max-w(530px){
        padding-right: 0;
        font-size: 14px;
        line-height: 16px;
      }
    }
  }

}
.works{
  padding: 47px 0 26px 0;
  @include max-w(1144px){
    padding: 18px 0 26px 0;
  }
  @include max-w(530px){
    padding-bottom: 0;
  }
  h2{
    @include max-w(992px){
      margin: 5px 0 36px 0;
    }
    @include max-w(530px){
      margin: 5px 0 -12px 0;
    }
  }

  &__bloks {
    padding: 7px 0;
    counter-reset: number;
    @include max-w(920px){
      justify-content: space-evenly;
    }
  }
  &__block{
    max-width: 357px;
    width: 100%;
    padding-left: 49px;
    @include max-w(1144px){
      max-width: 295px;
    }
    @include max-w(992px){
      max-width: 280px;
    }
    h5{
      text-transform: uppercase;
      margin-bottom: 0;
      position: relative;
      &:before{
        content: "";
        display: inline-block;
        background: url("../images/check.png") center no-repeat;
        height: 30px;
        width: 30px;
        position: absolute;
        left: -49px;
        top: -4px;
      }
    }
    p{
      color: $text-list;
      position: relative;
      &:before{
        counter-increment: number;
        content: counter(number);
        font-family: RobotoBold, sans-serif;
        font-size: 37px;
        color: #cfcfcf;
        position: absolute;
        left: -42px;
        top: 17px;
      }
    }
  }
}
.advisors{
  margin: 48px 0;

  @include max-w(1140px){
    margin: 19px 0 48px 0;
    h2{
      margin-top: 24px;
    }
  }
  @include max-w(992px){
    margin-bottom: 31px;
    h2{
      margin-top: 30px;
    }
  }
  @include max-w(530px){
    margin-bottom: 16px;
    h2{
      margin-top: 14px;
    }
  }
  &__text {
    padding: 6px 10px;
    @include max-w(530px){
      padding-top: 0;
    }
  }
  .full-container{
    background-color: #caddf3;
    padding: 10px 0 32px 0;
    @include max-w(1140px){
      padding: 10px 0 48px 0;
    }
    @include max-w(992px){
      padding: 10px 0 26px 0;
    }
    @include max-w(530px){
      padding-bottom: 10px;
    }
    &.v-line{
      &:before{
        top: -48px;
        left: -36px;
      }
      &:after{
        bottom: -38px;
        right: -36px;
      }
    }
  }
  .container{
    max-width: 1240px;
  }
  .flex-wrapper-holder{
    z-index:2;
    @include max-w(530px){
      max-width: 220px;
      margin: 0 auto;
    }
  }
  p{
    font-size: 18px;
    color: $text-light-grey;
    line-height: 25px;
    text-align: center;
    @include max-w(1140px){
      font-size: 17px;
      line-height: 24px;
    }
    @include max-w(530px){
      font-size: 14px;
      line-height: 17px;
      margin-top: 0;
      margin-bottom: 21px;
    }
  }
  .carousel{
    padding: 26px 0;
    @include max-w(1140px){
      padding: 6px 0;
    }
    @include max-w(992px){
      max-width: 720px;
      margin: 0 auto;
    }
    .item{
      max-width: 263px;
      margin: 0 auto;
      @include max-w(1140px){
        max-width: 220px;
      }
      .media-holder{
        height: 283px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
      }
      .detail-container{
        h4{
          text-align: center;
          margin: 8px 0;
          text-transform: uppercase;
          &:after{
            max-width: 242px;
            bottom: -7px;
          }
          a{
            color: $text-color;
          }
        }
        p{
          margin: 17px 0;
          line-height: 19px;
          &>*{
            display: inline-block;
            font-size: 16px;
            width: 100%;
            text-align: center;
          }
          strong{
            font-family: RobotoBold, sans-serif;
          }
        }
      }
    }
    .flex-item{
      z-index:2;
      @include max-w(530px){
        //max-width: 220px;
      }
    }
  }
  .flex-theme-default .flex-navigation {
    text-align: center;
    position: absolute;
    width: 100%;
    height: 100%;
    top: -57px;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .flex-next,.flex-prev{
      padding: 23px 11px;
      line-height: 0;
      transform: translateX(45px);
      background-color: transparent;
      border: 1px solid #bababa;
      border-radius: 0;
      color: #bababa;
      font-size: 35px;
      transition: all .3s;
      &:hover{
        line-height: 0;
        border: 1px solid $blue;
        color: $blue;
        transition: all .3s;
        @include max-w(530px){
          border: none;
        }
      }
    }
    .flex-next{
      transform: translateX(45px);
      @include max-w(530px){
        transform: translateX(6px);
        border: none;
      }
    }
    .flex-prev{
      transform: translateX(-45px);
      @include max-w(530px){
        transform: translateX(-6px);
        border: none;
      }
    }
  }
}
.faq{
  padding: 35px 0 0 0;
  @include max-w(1140px){
    padding-top: 8px;
  }
  h2{
    margin: 0;
  }
  .list-questions{
    max-width: 1096px;
    padding: 47px 0;
    @include max-w(992px){
      padding-bottom: 34px;
    }
    @include max-w(530px){
      padding-bottom: 33px;
    }
    li.active{
      &:before{
        width: 39px;
        background-color: $blue;
        left: 29px;
        transition: all .3s;
        @include max-w(1140px){
          padding-left: -9px;
          left: -11px;
        }
        @include max-w(530px){
          top: 9px;
          left: -36px;
        }
      }
      a{
        color: $blue;
        border-color: $blue;
        transition: all .3s;
      }
    }
    li{
      position: relative;
      padding-left: 98px;
      @include max-w(1140px){
        padding-left: 60px;
      }
      @include max-w(530px){
        padding-left: 13px;
      }
      &:before{
        content:"";
        display: inline-block;
        width: 19px;
        height: 1px;
        background-color: $grey;
        position: absolute;
        top: 11px;
        left: 49px;
        transition: all .3s;
        @include max-w(1140px){
          padding-left: 11px;
          left: 11px;
        }
        @include max-w(530px){
          top: 9px;
          left: -16px;
        }
      }
    }
    h4{
      margin-top: 14px;
      margin-bottom: 6px;
      @include max-w(530px){
        font-size: 14px;
        margin-bottom: 1px;
      }
    }
    p{
      font-size: 18px;
      margin: 0 0 10px 0;
      overflow: hidden;
      height: 0;
      opacity: 0;
      transition: all .3s;
    }
    p.active{
      height: 100%;
      opacity: 1;
      transition: all .3s;
    }
    a{
      display: inline-block;
      width: 24px;
      height: 24px;
      font-size: 11px;
      color: $text-color;
      border-radius: 50%;
      border: 1px solid $grey;
      text-align: center;
      vertical-align: middle;
      padding: 4px 0;
      float: right;
      transition: all .3s;
      &:hover{
        color: $blue;
        border-color: $blue;
        transition: all .3s;
    }
    }
  }
}
.banner-bottom{
  padding-top: 35px;
  @include max-w(1140px){
    padding-top: 0px;
  }
  &__text-block{
    max-width: 710px;
    @include max-w(1140px){
      max-width: 618px;
    }
    @include max-w(968px){
      max-width: 68%;
    }
    @include max-w(530px){
      max-width: 100%;
    }
    h3{
      font-size: 25px;
      line-height: 35px;
      margin: 0;
      @include max-w(1140px){
        font-size: 22px;
        line-height: 30px;
      }
      @include max-w(530px){
        font-size: 18px;
        line-height: 19px;
      }
    }
    p{
      margin: 57px 0;
      font-size: 18px;
      @include max-w(1140px){
        font-size: 17px;
        line-height: 12px;
        margin: 40px 0 47px 0;
      }
      @include max-w(968px){
        padding-right: 50px;
        line-height: 18px;
      }
      @include max-w(530px){
        padding-right: 0;
        line-height: 19px;
        margin: 37px 0 44px 0;
      }
      span{
        //padding-bottom: 12px;
        //display: inline-block;
      }
      strong{
        padding-top: 12px;
        display: inline-block;
        font-family: RobotoBold, sans-serif;
      }
    }
  }
  .full-container{
    padding: 91px 0 56px 0;
    //background: url("../images/banner-bottom.jpg") center top/cover;
    @include max-w(1140px){
      padding: 81px 0 56px 0;
    }
    @include max-w(968px){
      background-position: 74% center!important;
    }
    @include max-w(530px){
      padding: 43px 0 1px 0;
    }
    &:before{
      bottom: -40px;
      left: -36px;
    }
    &:after{
      height: 260px;
      right: -36px;
      top: -38px;
    }
  }
}